import React, { Component } from "react";
import SearchResults from "./SearchResults";
import API from "../utils/API";
import shuffleArr from "../utils/shuffleArr";
import { GridList } from 'material-ui/GridList';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

class SearchResultContainer extends Component {

  state = {
    score: 0,
    picked: [],
    trucks: []
  };

  // When this component mounts, search the Giphy API for pictures of kittens
  componentDidMount() {
    this.searchPixabay("trucks");
  }

  searchPixabay = query => {
    API.search(query)
      .then(res => this.setState({ trucks: res.data.hits }))
      .catch(err => console.log(err));
  };


  handleClickPic = event => {
    this.handleShufflePic();
    const id = event.target.attributes.getNamedItem("key").value;
    console.log(id)
  }
  

  handleShufflePic = () => {
    let trucksArr = this.state.trucks;
    let shuffledArr = shuffleArr(trucksArr);
    this.setState({ trucks: shuffledArr });
  }

  render(){
      return(
        <MuiThemeProvider>
        <GridList cols={3}>
        {this.state.trucks.map(truck => (
        <SearchResults handleClickPic={this.handleClickPic}  />
        ))};
        </GridList>
         </MuiThemeProvider>
         )
  }
}


export default SearchResultContainer;



